.hoveractivity .hover-area .icon-home {
	 width: 80px;
	 height: 80px;
	 position: absolute;
	 bottom: 91px;
	 left: calc(50% - 40px);
	 transform-origin: bottom center;
	 transform-style: preserve-3d;
	 transform: scale(0);
	 transition: transform 225ms ease-out;
}
.hoveractivity .hover-area .icon-home .roof {
	 height: 90px;
	 position: absolute;
	 left: 50%;
	 transform-origin: bottom center;
	 transform-style: preserve-3d;
	 transform: translateZ(45px) rotateX(90deg);
}
.hoveractivity .hover-area .icon-home .roof.roof.roof-1 {
