    position: relative;
    margin: 0;
}

.explainer .title {
	font-size: 70px;
    text-align: center;
}

.explainer .counter {
    text-align: center;
}

.explainer .text-container {
	width: 60vw;
	height:  50vh;
	background-color: #9e8772;
	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	position: relative;
	z-index: 2;
    border-radius: 2px;
	color:  black;
