    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.explainer .text-container:hover {
    cursor: pointer;
}

.explainer .text-container .number {
	font-family: 'Old Standard TT', serif;
	font-size: 1000%;
	position: absolute;
	top: 0;
	left: 0;
	height: 60%;
	width: 40%;
	text-align: center;
	display: flex;
    align-items: flex-end;
    justify-content: center;
}

.explainer .text-container .instructions {
	position: absolute;
	right: 0;
	bottom: 0;
	height: 50%;
	width: 62%;
	padding: 5%;
	font-size: 200%;
}

.explainer .bg {
	z-index: 1;
	position: absolute;
}

.explainer .bg.topRight {
    top: -5%;
    right: 10px;
    width: 20%;
    transform: rotate(-90deg);
}

.explainer .bg.bottomRight {
	right: -100px;
	bottom:  -300px;
}


