@import url("https://fonts.googleapis.com/css?family=Montserrat:400,400i,700");
 .hoveractivity {
	 font-family: Montserrat, sans-serif;
	 font-size: 28px;
	 background-color: #EDD4B4;
	 width: 100vw;
	 height: 100vh;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: column;
   overflow: hidden;
}

.hoveractivity .btn-challenge {
	 color: #ffefe0;
	 font-family: inherit;
	 font-size: inherit;
	 font-weight: bold;
	 background-color: #ebbb8e;
	 width: 195px;
	 height: 75px;
	 padding: 0;
	 border: 0;
	 margin: 0;
	 outline: 0;
	 flex-grow: 0;
	 flex-shrink: 0;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: column;
	 border-radius: 12px;
	 position: relative;
	 transition: background-color 180ms ease-out;
}
.hoveractivity .btn-challenge > small {
	 color: #ffefe0;
	 font-size: 0.6em;
	 font-weight: normal;
}
.hoveractivity .btn-challenge:hover {
	 background-color: #cfa682;
}
.hoveractivity .btn-challenge:active, .btn-challenge:hover:active {
	 background-color: #cfa682;
}
.hoveractivity .hover-area {
	 width: 195px;
	 height: 75px;
	 display: grid;
	 grid-template-columns: repeat(13, 15px);
	 grid-template-rows: repeat(5, 15px);
	 position: absolute;
	 top: 0;
	 left: 0;
	 perspective: 500px;
}
 .hoveractivity .hover-area .hover-col {
	 z-index: 1;
}
 .hover-area .hover-col:nth-child(1):hover ~ .icon-home {
	 transform: scale(1) rotateX(45deg) rotateY(-90deg);
	 transition: transform 380ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
